                <ul>
                    <li><a class="support" href="#"><img class="img-fluid" src="../assets/img/support.svg" alt=""></a></li>
                    <li>
                        <a class="search-box" href="javascript:void(0)">
                            <div class="form-group">
                                <input type="search" v-on:keyup.enter="searchLead" v-model="keyword" class="form-control" placeholder="Search...">
                                <img class="img-fluid sear-icon" v-on:click="toggleHeaderSearchIcon" src="../assets/img/search.svg" alt="">
                            </div>
