                    </li>
                    <li><a class="bells" href="#" v-on:click="toggleNotitab" ><img class="img-fluid" src="../assets/img/Bell.svg" alt=""> <span v-if="count > 0">{{count}}</span></a>
                        <div class="notif-tabs">
                            <ul class="nav nav-tabs" id="myTab" role="tablist">
                                <li class="nav-item">
                                <a class="nav-link active" id="home-tab" data-toggle="tab" href="#home" role="tab" aria-controls="home" aria-selected="true">Fresh</a>
                                </li>
                                <li class="nav-item">
                                <a class="nav-link border-right-0" id="profile-tab" data-toggle="tab" href="#profile" role="tab" aria-controls="profile" aria-selected="false">Merged</a>
                                </li>
                            </ul>
                            <div class="tab-content" id="myTabContent">
                                <simplebar class="tab-pane fade show active" id="home" role="tabpanel" aria-labelledby="home-tab" style="max-height: 300px">
                                    <router-link v-for="noti in notifications" :key="noti.id" :to="{ name: 'LeadDetail',params: { id: noti.data.id }}">
                                        {{noti}}
                                        {{noti.notifiable_id}} - {{user}}
                                        <div class="notif-irem" v-if="noti.data.noti_type == 1 && noti.notifiable_id == user">
                                            <div class="noti-title">
                                                <h3>{{noti.data.name}}</h3>
                                                <p>{{noti.data.lead_type}}</p>
                                                <p>{{noti.data.date}}</p>
                                            </div>
                                            <div class="noti-action" v-if="noti.data.touch_status == 0">
                                                <a href="#">untouched</a>
                                            </div>
                                        </div>
                                    </router-link>
                                    <div class="notif-irem" v-if="notifications.length == 0">
                                        <p>No notification found</p>
                                    </div>
                                    <!-- <div class="notif-irem">
                                        <div class="noti-title">
                                            <h3>javin a. lumpkin</h3>
                                            <p>internet led</p>
                                        </div>
                                        <div class="noti-action">
                                            <a href="#">untouched</a>
                                        </div>
                                    </div> -->
                                </simplebar>
                                <simplebar class="tab-pane fade" id="profile" role="tabpanel" aria-labelledby="profile-tab" style="max-height: 300px">
                                    <router-link v-for="noti in notifications" :key="noti.id" :to="{ name: 'LeadDetail',params: { id: noti.data.id }}">
                                        {{noti}}
                                        {{noti.notifiable_id}} - {{user}}
                                        <div class="notif-irem" v-if="noti.data.noti_type == 2 && noti.notifiable_id == user">
                                            <div class="noti-title">
                                                <h3>{{noti.data.name}}</h3>
                                                <p>{{noti.data.lead_type}}</p>
                                                <p>{{noti.data.date}}</p>
                                            </div>
                                            <div class="noti-action">
                                                <a href="#">untouched</a>
                                            </div>
                                        </div>
                                    </router-link>
                                    <div class="notif-irem" v-if="notifications.length == 0">
                                        <p>No notification found</p>
                                    </div>
                                </simplebar>
                            </div>
                        </div>
                    </li>
                    <li><a class="lead-btn" href="#" data-dismiss="modal" aria-label="Close" data-toggle="modal" data-target="#addNewLeadModal" data-backdrop="static" data-keyboard="false">Add Lead Sheet <img class="img-fluid" src="../assets/img/plus.png" alt=""></a></li>
                </ul>
            </div>
        </div>
        <!-- notification -->
        </div>

        <!-- Add Lead Modal -->
        <div class="modal fade" id="addNewLeadModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">Add Lead</h5>
                        <a class="close" data-dismiss="modal" aria-label="Close"  v-on:click="cancelCrop">
                        <span aria-hidden="true"><img class="img-fluid" src="../assets/img/23. Closex.svg" alt=""></span>
                        </a>
                    </div>
                    <form v-on:submit.prevent = "addNewLead">
                    <div class="modal-body">
                        <div v-show="doCrop">
                            <vue-cropper :aspect-ratio="1 / 1" :view-mode="1" ref="cropper" :src="cropImgUrl" alt="Source Image"></vue-cropper>
                        </div>
                        <div v-show="!doCrop">
                            <div class="user-img">
                                <div class="button-wrapper">
                                    <span class="label">
                                        <img class="img-fluid" v-bind:src="imgUrl" alt="">
                                    </span>
                                    <input type="file" ref="file" accept="image/*" v-on:change="handleFileUpload" class="upload upload-box" placeholder="Upload File">
                                </div>
                                <div class="edit-pro">
                                    <img class="img-fluid" src="../assets/img/edit.svg" alt="">
                                </div>
                            </div>
                            <div class="add-member add-lead">
                            
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                    <label>First Name</label>
                                    <input type="text" class="form-control" v-model="lead_.fname" placeholder="Jeanne">
                                    </div>
                                    <div class="form-group col-md-6">
                                    <label>Last Name</label>
                                    <input type="text" class="form-control" v-model="lead_.lname" placeholder="Acevedo">
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label>Email Address</label>
                                    <input type="email" class="form-control" v-model="lead_.email" placeholder="example@gmail.com">
                                </div>
                                <div class="form-group">
                                    <label>Phone number</label>
                                    <vue-tel-input v-model="lead_.phone" v-bind="{mode:'international',dynamicPlaceholder:true,inputId:'phone',enabledCountryCode:false,maxLen:15,validCharactersOnly:true,onlyCountries:['us','pk'],defaultCountry:'us',placeholder:'Enter a phone number'}" ></vue-tel-input>
                                    <!-- <input type="text" class="form-control" v-model="lead_.phone" placeholder="+123-22-223-00"> -->
                                </div>
                                <div class="form-group">
                                    <label>Lead Type</label>
                                    <select class="form-control" v-model="lead_.type" >
                                        <option value="">Select type</option>
                                        <option v-for="type in LTypes" :value="type" :key="type.lt_id">{{type.lt_name}}</option>
                                    </select>
                                </div>
                                <div class="form-group">
                                    <label>Lead Source</label>
                                    <select class="form-control" v-model="lead_.source" >
                                        <option value="">Select source</option>
                                        <option v-for="source in LSources" :value="source.ls_id" :key="source.ls_id" >{{source.ls_name}}</option>
                                    </select>
                                </div>
                                <div class="form-group Vehicle">
                                    <label>Vehicle of Interest</label>
                                    <input type="search" v-model="lead_.voi" class="form-control" placeholder="Type of Interest">
                                    <img class="img-fluid Vehi-img" src="../assets/img/search.svg" alt="">
                                </div>
                                <div class="form-group">
                                    <label>Trad In</label>
                                    <input type="text" v-model="lead_.tradeIn" class="form-control" placeholder="Enter Vin#">
                                </div>

                                
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <!-- <a href="#" class="add-btn"><img class="img-fluid" src="../assets/img/22. Delete.svg" alt=""> Cancel</a> -->
                        <a  v-if="doCrop" href="#" class="add-btn" data-dismiss="modal" v-on:click="cancelCrop"> Cancel</a>
                        <button type="button" v-on:click="cropIt" class="rem-btn" v-if="doCrop">Crop</button>
                        <button  v-if="!doCrop" type="submit" class="rem-btn"><img class="img-fluid" src="../assets/img/23. Close.svg" alt=""> Add</button>
                    </div>
                    </form>
                </div>
            </div>
        </div>

        <!-- Add Lead Modal -->
    </div>
</template>

<script>

import axios from 'axios'
import $ from 'jquery'
import VueCropper from 'vue-cropperjs';
import 'cropperjs/dist/cropper.css';
import simplebar from 'simplebar-vue';
import 'simplebar/dist/simplebar.min.css';
export default {
    name: 'TheHeader',
    components: {VueCropper,simplebar},
    props: ['headerType'],
    data(){
		return{
            keyword:'',
            count:0,
            notifications:{},
            LSources:{},
            imgUrl: require('../assets/img/profile.svg'),
            cropImgUrl: null,
            user:'',
            doCrop:false,
            LTypes:{},
            lead_ : {fname:'',lname:'',email:'',phone:'',source:'',type:'',voi:'',tradeIn:'',img:''}
        }
    },
    created(){
        this.getLeadSource()
        this.getLeadType()
        this.getNotifications()

    },
    mounted(){
        window.Echo.channel('notiChannel') //Should be Channel Name
        .listen('NotiEvent', (e) => {
            console.log(e)
            this.count = e.count
            this.notifications = e.text
        });
    },
    methods:{
        getNotifications(){
            axios
			.get('notifications',{headers: {Authorization:'Bearer '+this.$storage.get('auth').token}})
			.then( res => {
                if(res.data){
                    this.count = res.data.count
                    this.notifications = res.data.text
                    this.user = res.data.user
                    console.log(this.user)
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        toggleNotitab(){
            if($(".notif-tabs").hasClass("opens") === false && this.count > 0){
                this.markAsRead()
            }
            $(".notif-tabs").toggleClass("opens");
        },
        toggleSidebar(){
            $(".menu-area").toggleClass("menu-full");
            $(".hide-item").toggleClass("show-item");
            $(".big-logo").toggleClass("show-logo");
            $(".audits-completed").toggleClass("menu-full-body");
        },
        toggleHeaderSearchIcon(){
            $(".search-box").toggleClass("full-search-box");
            $(".search-box .form-control").toggleClass("full-search-control");
        },
        markAsRead(){
            axios
			.get('mark_as_read',{headers: {Authorization:'Bearer '+this.$storage.get('auth').token}})
			.then( res => {
                this.count = 0;
                if(res.data){
                    this.count = 0;
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        addNewLead(){
            console.log(this.lead_)
			axios
			.post('add_new_lead',this.lead_,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
			.then( res => {
                if (res.data.success) {
                    $("#addNewLeadModal").modal('hide')
                    if(window.location.pathname != '/leads'){
                        this.$router.push('/leads')
                    }else{
                        this.$root.$emit('newLead', 'added')
                    }
                    this.lead_ = {fname:'',lname:'',email:'',phone:'',source:'',type:'',voi:'',tradeIn:'',img:''}
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        getLeadSource(){
			axios
			.get('lead_sources')
			.then( res => {
                if (res.data.success) {
                    this.LSources = res.data.success
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        getLeadType(){
			axios
			.get('lead_types')
			.then( res => {
                if (res.data.success) {
                    this.LTypes = res.data.success
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        handleFileUpload(e){
            const file = e.target.files[0];
            if (file.type.indexOf('image/') === -1) {
                alert('Please select an image file');
                return;
            }

            if (typeof FileReader === 'function') {
                const reader = new FileReader();
                reader.onload = (event) => {
                    this.cropImgUrl = event.target.result;
                    // rebuild cropperjs with the updated source
                    this.$refs.cropper.replace(event.target.result);
                };
                reader.readAsDataURL(file);
            } else {
                alert('Sorry, FileReader API not supported');
            }
            this.doCrop = true

            //this.imgUrl = URL.createObjectURL(this.$refs.file.files[0]);
            //console.log(this.imgUrl);
        },
        cropIt(){
            this.imgUrl = this.$refs.cropper.getCroppedCanvas().toDataURL()
            this.lead_.img = this.$refs.cropper.getCroppedCanvas().toDataURL()
            //console.log(this.register_.img)
            this.doCrop = false
        },
        cancelCrop(){
            this.doCrop = false
        },
        searchLead(){
            if(this.keyword != '' && this.keyword != undefined && this.keyword != 'undefined' && this.keyword != null ){
                this.$router.push({path: '/leads', query: {keyword: this.keyword}})
                if(window.location.pathname == '/leads'){
                    this.$root.$emit('newLead', 'added')
                }
            }
        }
    }
}
</script>